<template>
    <nav aria-label="breadcrumb" class="container pt-3" >
        <ol class="breadcrumb">
            <li class="breadcrumb-item" style="font-size: 18px;font-weight: bold;"><router-link to="/Dashboard/product" class="link-info" style="text-decoration:none;"><i class="bi bi-arrow-left"></i> 上一頁</router-link></li>
            <!-- <li class="breadcrumb-item active" aria-current="page">{{ product.title }}</li> -->
        </ol>
    </nav>
    <section class="py-5 text-center container">
        <div class="row py-lg-5">
            <div class="col-lg-6 col-md-8 mx-auto">
                <h1 class="fw-bold">沙發名稱 : {{data.chname}}</h1>
            </div>
        </div>
    </section>
    <div class="album pb-5">
        <div class="container">
            <div class="row row-cols-1 row-cols-sm-2 row-cols-md-3 g-3">
                <div v-for="(item, index) in data.imagesurl" :key="index" class="col">
                    <div class="card shadow-sm">
                        <img :src="item" class="card-img-top card-img-bottom" alt="..." @click.prevent="openModal(item)">
                    </div>
                </div>
            </div>
        </div>
    </div>
    <productmodal ref="productmodal" :imageurl="imageurlone"></productmodal>
</template>
<script>
import productmodal from '../components/productmodal.vue';
export default {
    data () {
        return {
            data: {},
            imageurlone: {

            }
        };
    },
    components: {
        productmodal
    },
    created () {
        this.data = JSON.parse(this.$route.query.obj);
        console.log(JSON.parse(this.$route.query.obj));
        // this.getProduct();
    },
    methods: {
        openModal (url) {
            this.imageurlone.url = url;
            this.imageurlone.name = this.data.chname;
            const productmodall = this.$refs.productmodal;
            productmodall.showModel();
        }
    }
};
</script>
